import React, { useEffect } from "react";
import "../../App.css";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import { Link, useParams } from "react-router-dom";

interface recommendedType {
  address: string;
  categories: string;
  domainId: string;
  extra_name: string;
  image_url: string;
  logo: string;
  name: string;
  rating: string;
}

//

const RecommendedList = ({
  recommendedData,
}: {
  recommendedData: Array<recommendedType>;
}) => {
  // SliderList
  const SliderList = {
    backgroundRepeat: "no-repeat",
    display: "flex",
    height: "100%",
    backgroundSize: "26px",
    padding: "0 5px",
  };

  //   OwlCarousel
  const customPrevIcon = '<i class="fa fa-chevron-left"></i>';
  const customNextIcon = '<i class="fa fa-chevron-left"></i>';
  const RecommendedProps = {
    loop: true,
    autoplay: true,
    autoplaytimeout: 4000,
    responsive: {
      1100: {
        items: 4,
      },
      724: {
        items: 3,
      },
      300: {
        items: 1,
      },
    },
  };

  const { domainName } = useParams();

  const classes = ["", "bg-yellow", "bg-blue", "bg-dryellow"];

  return (
    <div>
      <section className="recommended_sec section-gap">
        <div className="container">
          <h2
            className="titleh2 textmobi-center"
            style={{ paddingLeft: "20px", fontWeight: "bold" }}
          >
            Recommended
          </h2>
          {recommendedData && !!recommendedData.length && (
            <div
              className="owl-carousel owl-theme work-list"
              style={SliderList}
            >
              <OwlCarousel
                className="owl-theme"
                {...RecommendedProps}
                navText={[customNextIcon, customPrevIcon]}
                nav
              >
                {recommendedData.map((data: any, index: number) => {
                  var activeClass =
                    "sub_head " + classes[index % classes.length];
                  return (
                    <div className="col-12 item" key={index} style={SliderList}>
                      <div className="detail">
                        <Link to={`/salons/${data.domainName}`}>
                          <div className="circle-icon">
                            <img
                              src={data.image_url}
                              onError={({ currentTarget }) => {
                                currentTarget.onerror = null;
                                currentTarget.src = "/images/placeholder/store.jpg";
                              }}
                              alt={data.categories}
                              style={{ maxWidth: "100%" }}
                              width={352}
                              height={235}
                            />
                          </div>
                          <div className="Content">
                            {data.categories && (
                              <span className={activeClass}>
                                {data.categories}
                              </span>
                            )}
                            <h3>{data.name}</h3>
                            {/* <span className="rating">{data.rating}</span> */}
                            {data.address.length > 50
                              ? `${data.address.substring(0, 50)}...`
                              : data.address}
                          </div>
                        </Link>
                      </div>
                    </div>
                  );
                })}
              </OwlCarousel>
            </div>
          )}
        </div>
      </section>
    </div>
  );
};

export default RecommendedList;
