import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

// File import
import Home from "./pages/home/Home";
import ListingDetail from "./components/ListingDetails/list/ListingDetail";
import Footer from "./components/footer/Footer";
import MybeShowFooter from "./components/mybeShowFooter/MybeShowFooter";
import Register from "./pages/register/Register";
import Login from "./pages/login/Login";
import ForgetPassword from "./pages/forgetPassword/ForgetPassword";
import Category from "./components/category/Category";
import Booking from "./pages/booking/Booking";

const App = () => {
  return (
    <>
      <Router>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/register" element={<Register />} />
          <Route path="/login" element={<Login />} />
          <Route path="/forgetPassword" element={<ForgetPassword />} />
          <Route path="/booking/:domainName" element={<Booking />} />
          <Route path="/salons/:domainName" element={<ListingDetail />} />
          <Route path="/category/:categoryId" element={<Category />} />
        </Routes>

        <MybeShowFooter>
          <Footer />
        </MybeShowFooter>
      </Router>
      <ToastContainer autoClose={4000} />
    </>
  );
};

export default App;
